html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

.icon-find {
  width: 1em;
  height: 1em;
  fill: #FFFFFF;
}

.icon-key {
  width: 1em;
  height: 1em;
  fill: #FFFFFF;
}

.container {
  background: red;
  width: 100%;
  height: 100vh;
}

.container h1 {
  color: yellow;
  font-family: "Verdana";
  font-weight: bold;
  font-size: 7em;
  text-align: center;
  line-height: 400px;
  text-shadow: 2px 2px black;
}

.photo {
  width: 300px;
  height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  border-radius: 150px;
  margin: 0 auto;
  border: 2px solid white;
  margin-top: -100px;
}

body {
  font-family: "Verdana";
}

.main__content {
  display: flex;
  flex: 100%;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  background-image: linear-gradient(to right bottom, #2a2b2c, #3e3f40, #535454, #69696a, #808080);
}

.main__content h2 {
  letter-spacing: 2px;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-weight: normal;
  margin-top: 10px;
  margin-bottom: 10px;
}

.main_menu {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 100;
  left: 0;
  top: 0;
  background: #1a202b;
  height: 40px;
  font-weight: bold;
}

.main_menu .signIn a {
  text-decoration: none;
  position: absolute;
  right: 20px;
  top: 12px;
  color: white;
}

.main_menu .signIn a .icon-key {
  margin-bottom: -2px;
}

.main_menu .signIn a:hover {
  color: cyan;
}

.main_menu .signIn a:hover .icon-key {
  fill: cyan;
}

.main_menu .logo {
  color: white;
}

.main_menu .search {
  position: absolute;
  right: 130px;
}

.main_menu .search input[type=text] {
  width: 200px;
  height: 25px;
  background: #4c4c4c;
  background: -moz-linear-gradient(top, #4c4c4c 0%, #595959 12%, #666666 25%, #474747 39%, #2c2c2c 50%, black 51%, #111111 60%, #2b2b2b 76%, #1c1c1c 91%, #131313 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #4c4c4c), color-stop(12%, #595959), color-stop(25%, #666666), color-stop(39%, #474747), color-stop(50%, #2c2c2c), color-stop(51%, black), color-stop(60%, #111111), color-stop(76%, #2b2b2b), color-stop(91%, #1c1c1c), color-stop(100%, #131313));
  background: -webkit-linear-gradient(top, #4c4c4c 0%, #595959 12%, #666666 25%, #474747 39%, #2c2c2c 50%, black 51%, #111111 60%, #2b2b2b 76%, #1c1c1c 91%, #131313 100%);
  background: -o-linear-gradient(top, #4c4c4c 0%, #595959 12%, #666666 25%, #474747 39%, #2c2c2c 50%, black 51%, #111111 60%, #2b2b2b 76%, #1c1c1c 91%, #131313 100%);
  background: -ms-linear-gradient(top, #4c4c4c 0%, #595959 12%, #666666 25%, #474747 39%, #2c2c2c 50%, black 51%, #111111 60%, #2b2b2b 76%, #1c1c1c 91%, #131313 100%);
  background: linear-gradient(to bottom, #4c4c4c 0%, #595959 12%, #666666 25%, #474747 39%, #2c2c2c 50%, black 51%, #111111 60%, #2b2b2b 76%, #1c1c1c 91%, #131313 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4c4c4c', endColorstr='#131313', GradientType=0 );
  border-radius: 5px;
  -webkit-transition: width .5s;
  transition: width .5s;
}

.main_menu .search input[type=text]:focus {
  background: white;
  width: 250px;
}

.main_menu .search input[type=text]::placeholder {
  color: cyan;
  text-align: center;
}

.main_menu .search .icon-find {
  margin-top: 5px;
  margin-left: -25px;
}

.main_menu .search .icon-find:focus {
  visibility: hidden;
}

.main_menu .filter_menu {
  position: absolute;
  left: 20%;
}

.main_menu .filter_menu select {
  background: #1a202b;
  color: white;
  border-radius: 5px;
  border: 0;
  text-decoration: underline;
}

.new_muvies {
  display: flex;
  flex-direction: column;
}

.new_film_block {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.new_film {
  width: 230px;
  height: 355px;
  margin-left: 2px;
  margin-right: 2px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}

.new_film:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.new_film:hover .unvisible {
  visibility: visible;
}

.new_film .unvisible {
  visibility: hidden;
  text-align: left;
  width: 100%;
  height: 100%;
  padding: 5px;
  color: white;
  font-weight: bold;
  background: -moz-linear-gradient(top, #000621 0%, rgba(86, 3, 19, 0.19) 42%, rgba(204, 0, 0, 0) 100%);
  background: -webkit-linear-gradient(top, #000621 0%, rgba(86, 3, 19, 0.19) 42%, rgba(204, 0, 0, 0) 100%);
  background: linear-gradient(to bottom, #000621 0%, rgba(86, 3, 19, 0.19) 42%, rgba(204, 0, 0, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000621', endColorstr='#00cc0000',GradientType=0 );
}

