body {
	font-family: "Verdana"; }

.main__content {
	display: flex;
	flex: 100%;
	flex-direction: column;
	align-items: center;
	margin-top: 40px;
	background-image: linear-gradient(to right bottom, #2a2b2c, #3e3f40, #535454, #69696a, #808080);
	h2 {
		letter-spacing: 2px;
		text-align: center;
		color: white;
		text-transform: uppercase;
		font-weight: normal;
		margin-top: 10px;
		margin-bottom: 10px; } }
.main_menu {
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	z-index: 100;
	left: 0;
	top: 0;
	background: #1a202b;
	height: 40px;
	font-weight: bold;

	.signIn a {
		text-decoration: none;
		position: absolute;
		right: 20px;
		top: 12px;
		color: white;
		.icon-key {
			margin-bottom: -2px; } }
	.signIn a:hover {
		color: cyan;
		.icon-key {
			fill: cyan; } }
	.logo {
		color: white; }
	.search {
		position: absolute;
		right: 130px;
		input[type=text] {
			width: 200px;
			height: 25px;
			background: rgba(76,76,76,1);
			background: -moz-linear-gradient(top, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 12%, rgba(102,102,102,1) 25%, rgba(71,71,71,1) 39%, rgba(44,44,44,1) 50%, rgba(0,0,0,1) 51%, rgba(17,17,17,1) 60%, rgba(43,43,43,1) 76%, rgba(28,28,28,1) 91%, rgba(19,19,19,1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(76,76,76,1)), color-stop(12%, rgba(89,89,89,1)), color-stop(25%, rgba(102,102,102,1)), color-stop(39%, rgba(71,71,71,1)), color-stop(50%, rgba(44,44,44,1)), color-stop(51%, rgba(0,0,0,1)), color-stop(60%, rgba(17,17,17,1)), color-stop(76%, rgba(43,43,43,1)), color-stop(91%, rgba(28,28,28,1)), color-stop(100%, rgba(19,19,19,1)));
			background: -webkit-linear-gradient(top, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 12%, rgba(102,102,102,1) 25%, rgba(71,71,71,1) 39%, rgba(44,44,44,1) 50%, rgba(0,0,0,1) 51%, rgba(17,17,17,1) 60%, rgba(43,43,43,1) 76%, rgba(28,28,28,1) 91%, rgba(19,19,19,1) 100%);
			background: -o-linear-gradient(top, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 12%, rgba(102,102,102,1) 25%, rgba(71,71,71,1) 39%, rgba(44,44,44,1) 50%, rgba(0,0,0,1) 51%, rgba(17,17,17,1) 60%, rgba(43,43,43,1) 76%, rgba(28,28,28,1) 91%, rgba(19,19,19,1) 100%);
			background: -ms-linear-gradient(top, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 12%, rgba(102,102,102,1) 25%, rgba(71,71,71,1) 39%, rgba(44,44,44,1) 50%, rgba(0,0,0,1) 51%, rgba(17,17,17,1) 60%, rgba(43,43,43,1) 76%, rgba(28,28,28,1) 91%, rgba(19,19,19,1) 100%);
			background: linear-gradient(to bottom, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 12%, rgba(102,102,102,1) 25%, rgba(71,71,71,1) 39%, rgba(44,44,44,1) 50%, rgba(0,0,0,1) 51%, rgba(17,17,17,1) 60%, rgba(43,43,43,1) 76%, rgba(28,28,28,1) 91%, rgba(19,19,19,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4c4c4c', endColorstr='#131313', GradientType=0 );
			border-radius: 5px;
			-webkit-transition: width .5s;
			transition: width .5s;
			&:focus {
				background: white;
				width: 250px; } }

		input[type=text]::placeholder {
			color: cyan;
			text-align: center; }

		.icon-find {
			margin-top: 5px;
			margin-left: -25px;
			// width: .8em
			// height: .8em
			&:focus {
				visibility: hidden; } } }
	.filter_menu {
		position: absolute;
		left: 20%;
		select {
			background: #1a202b;
			color: white;
			border-radius: 5px;
			border: 0;
			text-decoration: underline; } } }








.new_muvies {
	display: flex;
	flex-direction: column; }

.new_film_block {
	display: flex;
	flex-direction: row;
	justify-content: center; }
.new_film {
	width: 230px;
	height: 355px;
	margin-left: 2px;
	margin-right: 2px;
	cursor: pointer;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;

	&:hover {

		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
		.unvisible {
			visibility: visible; } }
	.unvisible {
		visibility: hidden;
		text-align: left;
		width: 100%;
		height: 100%;
		padding: 5px;
		color: white;
		font-weight: bold;

		background: -moz-linear-gradient(top, rgba(0,6,33,1) 0%, rgba(86,3,19,0.19) 42%, rgba(204,0,0,0) 100%);
		background: -webkit-linear-gradient(top, rgba(0,6,33,1) 0%,rgba(86,3,19,0.19) 42%,rgba(204,0,0,0) 100%);
		background: linear-gradient(to bottom, rgba(0,6,33,1) 0%,rgba(86,3,19,0.19) 42%,rgba(204,0,0,0) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000621', endColorstr='#00cc0000',GradientType=0 ); }
	@media only screen and (max-width: 1200px) {
		&:nth-last-child(1) {
			display: none; } }
	@media only screen and (max-width: 950px) {
		&:nth-last-child(2) {
			display: none; } }
	@media only screen and (max-width: 400px) {
		width: 140px;
		height: 190px;
		&:nth-last-child(3) {
			display: none; } }

	&:nth-last-child(1) {
		background-image: url("../img/afisha/1.jpg"); }

	&:nth-last-child(2) {
		background-image: url("../img/afisha/2.jpg"); }

	&:nth-last-child(3) {
		background-image: url("../img/afisha/3.jpg"); }

	&:nth-last-child(4) {
		background-image: url("../img/afisha/1.jpg"); }

	&:nth-last-child(5) {
		background-image: url("../img/afisha/2.jpg") {

 } } }			// background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, #fff 100%)
.rendom_letter_movie {
	display: flex;
	flex-direction: column;
	margin-top: 10px; }

.letter {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 10px;
	a {
		margin-left: 4px;
		margin-right: 4px;
		color: white;
		text-decoration: none; } }


.film_letter_block {
	display: flex;
	flex-direction: row;
	justify-content: center; }
.film_letter {
	width: 100px;
	height: 150px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	margin-left: 2px;
	margin-right: 2px;
	&:nth-last-child(1) {
		background-image: url("../img/afisha/4.jpg"); }
	&:nth-last-child(2) {
		background-image: url("../img/afisha/5.jpg"); }
	&:nth-last-child(3) {
		background-image: url("../img/afisha/6.jpg"); }
	&:nth-last-child(4) {
		background-image: url("../img/afisha/7.jpg"); }
	&:nth-last-child(5) {
		background-image: url("../img/afisha/8.jpg"); }
	&:nth-last-child(6) {
		background-image: url("../img/afisha/9.jpg"); }
	&:nth-last-child(7) {
		background-image: url("../img/afisha/10.jpg"); }
	&:nth-last-child(8) {
		background-image: url("../img/afisha/8.jpg"); }
	&:nth-last-child(9) {
		background-image: url("../img/afisha/9.jpg"); }
	&:nth-last-child(10) {
		background-image: url("../img/afisha/10.jpg"); }
	@media only screen and (max-width: 1200px) {
		&:nth-last-child(1) {
			display: none; } }

	@media only screen and (max-width: 950px) {
		&:nth-last-child(2) {
			display: none; } }
	@media only screen and (max-width: 400px) {
		display: none; } }

.news {
	display: flex;
	flex-direction: row;
	justify-content: center; }

.news_box {
	display: flex;
	flex-direction: row;
	margin-right: 5px;
	margin-left: 5px;
	width: 400px;
	height: 180px;
	@media only screen and (max-width: 1200px) {
		&:nth-last-child(1) {
			display: none; } } }

.news_text {
	padding-left: 10px;
	background: #fff;
	overflow: hidden;
	text-overflow: ellipsis; }
.news_pic img {
	width: 120px;
	height: 180px; }

.last_films {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 850px;
	@media only screen and (min-width: 1400px) {
		width: 1020px; }
	@media only screen and (max-width: 400px) {
		width: 310px; } }




.film {
	width: 150px;
	height: 190px;
	margin: 10px;
	background-image: url("../img/afisha/8.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;

	@media only screen and (max-width: 400px) {
		margin: 2px; } }
.foother {
	width: 100%;
	height: 200px;
	background: #1a202b; }




